import { getMultiLang } from 'nc-lightapp-front';

/**
 * 初始化多语文件到组件内
 * @param {*} _this 
 * @param {*} resPath 数组
 * @param {*} moduleCode 
 * @param {*} callback 初始化模板回调
 */
function initLang(_this, resPath, moduleCode, callback) {
	// 初始化集合
	_this.lang = {};
	_this.inlt = null;

	let success = (lang, status, inlt) => {
		if (status) {
			_this.lang = lang;
			_this.inlt = inlt;
		}
		// 模板初始化
		callback();
	};
	getMultiLang({ moduleId: resPath, domainName: moduleCode, callback: success });
}

/**
 * 获取多语
 * @param {*} _this 
 * @param {*} resid 
 * @param {*} param 
 */
function getLangByResId(_this, resid, param) {
	let str = resid;
	if (param) {
		str = _this.inlt.get(resid, param);
		return str ? str : resid;
	} else {
		str = _this.lang[resid];
		return str ? str : resid;
	}
}

export { initLang, getLangByResId };
